import React,{ Component } from 'react';
import { NavLink } from 'react-router-dom';
import './main_contact.css';
import Mainbanner from './main_banner';
import Mainzixun from './main_zixun';

export default class Maincontact extends Component {
    componentDidMount(){
        const { BMap, BMAP_STATUS_SUCCESS } = window;
        const map = new BMap.Map("allmap"); // Map
        const point = new BMap.Point(119.220223, 34.598045); // 
        map.centerAndZoom(point, 17); // ,
        map.enableScrollWheelZoom(); //
        map.addControl(new BMap.NavigationControl());
        map.addControl(new BMap.ScaleControl());
        map.addControl(new BMap.OverviewMapControl());
        const marker = new BMap.Marker(point);
        map.addOverlay(marker);
        const infoWinOpts = {}
        const infoWin = new BMap.InfoWindow(
            "<div style={{textAlign:'left',color:'#1A6DAF'}}><p></p><p></p><p>18052310959()</p><p>18052310959</p><p>32-7A503</p></div>",
            infoWinOpts);
        marker.openInfoWindow(infoWin);
        marker.addEventListener("click", function () {
            this.openInfoWindow(infoWin);
        });
    }
    render(){
        return (
            <div>
                <Mainbanner />
                <div className="article">
                    <div className="title">
                        <div className="title_r">
                            
                            <NavLink to="/"></NavLink> - 
                        </div>
                    </div>
                    <div id="allmap" style={{height: '500px',maxWidth: '1220px',width: '80%',margin: '0 auto'}}></div>
                </div>
                <Mainzixun />
            </div>
        )
    }
}


