import React,{ Component } from 'react';
import $ from 'jquery';
import './fixnav.css';
import ly from '../images/liyang.jpg';

export default class Fixnav extends Component {
    constructor() {
        super();
        this.state = {

        }
        this.showewm = this.showewm.bind(this);
        this.hidewm = this.hidewm.bind(this);
        this.toTop = this.toTop.bind(this);
        this.tankuang = this.tankuang.bind(this);
    }
    showewm() {
        $('.erweima').stop().animate({
            width: '112px'
        })
    }
    hidewm() {
        $('.erweima').stop().animate({
            width: '0px'
        })
    }
    tankuang() {
        $('.motai').fadeIn();
    }
    toTop() {
        $('html,body').animate({
            scrollTop: 0
        }, 300)
    }
    render() {
        return(
            <div className="fix-nav">
                <ul>
                    <li className="fa fa-weixin" onMouseOver={this.showewm} onMouseOut={this.hidewm}></li>
                    <li className="fa fa-phone" onClick={this.tankuang}></li>
                    <li className="fa fa-chevron-up" onClick={this.toTop}></li>
                </ul>
                <div className="erweima">
                    <img src={ly} alt="erweima" />
                </div>
            </div>
        )
    }
}


