import React,{ Component } from 'react';
import $ from 'jquery';
import './motai.css';
import phone from '../images/phone.png';

class Motai extends Component {
    constructor(props){
        super(props);
        this.shuttan = this.shuttan.bind(this);
        this.stopShut = this.stopShut.bind(this);
    }
    shuttan() {
		$('.motai').fadeOut();
    }
    stopShut(e){
        e.stopPropagation();
    }
    render() {
        return(
            <div className="motai" style={{display: 'none'}} onClick={this.shuttan}>
                <div className="mo-inner" onClick={this.stopShut}>
                    <h2></h2>
                    <h3></h3>
                    <p><img src={phone} alt="" />&nbsp;&nbsp;18052310959</p>
                    <a href="tel:18052310959" className="boda" onClick={this.shuttan}></a>
                </div>
            </div>
        )
    }
}

export default Motai;


