import React, { Component } from 'react';
import { HashRouter, Route, NavLink, Switch } from 'react-router-dom';
import $ from 'jquery';
import './head.css';
import logo from '../images/logo3.png';
import nav_btn1 from '../images/nav-btn1.png';
import nav_btn2 from '../images/nav-btn2.png';
import MainIndex from '../main/main_index';
import MainGsfw from '../main/main_gsfw';
import Maincaishui from '../main/main_caishui';
import Maingsbg from '../main/main_gsbg';
import Mainwenti from '../main/main_wenti';
import Mainhyzx from '../main/main_hyzx';
import Maincontact from '../main/main_contact';

class Header extends Component {
	constructor(props){
		super(props);
		this.state = {
			nav_sm_show : true,
			btn_src : [nav_btn1,nav_btn2],
		}
		this.change_btn = this.change_btn.bind(this);
		this.tankuang = this.tankuang.bind(this);
		this.shuttan = this.shuttan.bind(this);
		this.turnLog = this.turnLog.bind(this);
	}
	change_btn() {
		var smstate = this.state.nav_sm_show;
		!smstate?$('.nav-sm').hide():$('.nav-sm').fadeIn();
		this.setState(prevState => ({
			nav_sm_show: !prevState.nav_sm_show
		}));
	}
	tankuang() {
		$('.motai').fadeIn();
	}
	shuttan() {
		$('.motai').fadeOut();
	}
	turnLog(){
		$('.log-reg').fadeIn();
		$('.log-tip').css({
			display: 'none'
		});
	}
	componentDidMount() {
		nav_top_lis();
		$(document).scroll(nav_top_lis);
		function nav_top_lis() {
			let scroll_top = $(document).scrollTop();
			let nav_top = $('.nav-wrapper').offset().top;
			if (scroll_top > nav_top) {
				$('.head-nav-fix').stop().slideDown(100);
			} else {
				$('.head-nav-fix').hide();
			}
		}
    }
	render() {
		return (
			<HashRouter>
				<div className="Main">
					<div className="headpage">
						<div className="container">
							<div className="row">
								<div className="headpage-wrapper hidden-sm hidden-xs">
									<ul className="clear">
										<li className="left">
											<i></i>
										</li>
										<li className="right">
											<a onClick={this.tankuang}></a>
										</li>
										<li className="right">
											<a onClick={this.turnLog}></a>
										</li>
									</ul>
								</div>
							</div>
						</div>
					</div>

					<div className="header">
						<div className="header-wrapper">
							<div className="navbar">
								<div className="navbar-logo">
									<NavLink className="logo" to="/">
										<img src={logo} alt="" />
									</NavLink>
									<div className="nav-btn">
										<img src={this.state.nav_sm_show?this.state.btn_src[0]:this.state.btn_src[1]} onClick={this.change_btn} alt="change_sm_btn" />
									</div>
								</div>
								<div className="nav-wrapper">									
									<ul className="clear">
										<li>
											<NavLink to="/" activeClassName="actived" exact></NavLink>
										</li>
										<li>
											<NavLink to="/gsfw" activeClassName="actived"></NavLink>
										</li>
										<li>
											<NavLink to="/caishui" activeClassName="actived"></NavLink>
										</li>
										<li>
											<NavLink to="/gsbg" activeClassName="actived"></NavLink>
										</li>
										<li>
											<NavLink to="/wenti" activeClassName="actived"></NavLink>
										</li>
										<li>
											<NavLink to="/hyzx" activeClassName="actived"></NavLink>
										</li>
										<li>
											<NavLink to="/women" activeClassName="actived"></NavLink>
										</li>
									</ul>
								</div>
								<ul className="nav-sm">
									<li>
										<NavLink to="/" activeClassName="actived" onClick={this.change_btn}></NavLink>
									</li>
									<li>
										<NavLink to="/gsfw" activeClassName="actived" onClick={this.change_btn}></NavLink>
									</li>
									<li>
										<NavLink to="/caishui" activeClassName="actived" onClick={this.change_btn}></NavLink>
									</li>
									<li>
										<NavLink to="/gsbg" activeClassName="actived" onClick={this.change_btn}></NavLink>
									</li>
									<li>
										<NavLink to="/wenti" activeClassName="actived" onClick={this.change_btn}></NavLink>
									</li>
									<li>
										<NavLink to="/hyzx" activeClassName="actived" onClick={this.change_btn}></NavLink>
									</li>
									<li>
										<NavLink to="/women" activeClassName="actived" onClick={this.change_btn}></NavLink>
									</li>
									<li>
										<a onClick={this.turnLog}></a>
									</li>
								</ul>
							</div>
						</div>
					</div>
					<div className="nav-wrapper head-nav-fix hidden-md hidden-sm hidden-xs" style={{display:'none'}}>
						<div className="head-nav-fix-wrapper">
							<NavLink to="/">
								<img src={logo} alt="logo" />
							</NavLink>
							<ul className="clear">
								<li>
									<NavLink to="/" activeClassName="actived"></NavLink>
								</li>
								<li>
									<NavLink to="/gsfw" activeClassName="actived"></NavLink>
								</li>
								<li>
									<NavLink to="/caishui" activeClassName="actived"></NavLink>
								</li>
								<li>
									<NavLink to="/gsbg" activeClassName="actived"></NavLink>
								</li>
								<li>
									<NavLink to="/wenti" activeClassName="actived"></NavLink>
								</li>
								<li>
									<NavLink to="/hyzx" activeClassName="actived"></NavLink>
								</li>
								<li>
									<NavLink to="/women" activeClassName="actived"></NavLink>
								</li>
								<li>
									<a onClick={this.turnLog}></a>
								</li>
							</ul>
						</div>
					</div>
					<Switch>
						<Route exact path="/" component={MainIndex}></Route>
						<Route path="/gsfw" component={MainGsfw}></Route>
						<Route path="/caishui" component={Maincaishui}></Route>
						<Route path="/gsbg" component={Maingsbg}></Route>
						<Route path="/wenti" component={Mainwenti}></Route>
						<Route path="/hyzx" component={Mainhyzx}></Route>
						<Route path="/women" component={Maincontact}></Route>
						<Route path="/*" component={MainIndex}></Route>
					</Switch>
				</div>
			</HashRouter>
		)
	}
}

export default Header;



