import React,{ Component } from 'react';
import $ from 'jquery';
import './main_zixun.css';

export default class Mainzixun extends Component {
    constructor(){
        super();
        this.state = {
            questions: []
        }
    }
    componentDidMount(){
        const _this = this;
        const hostname = window.location.hostname;
        setTimeout(()=>{
            _this.ajax ? _this.ajax : '';
            _this.ajax = $.ajax({
                url: 'http://' + hostname + ':3002/question',
                type: 'get',
                dateType: 'json',
                success: function(res){
                    _this.setState({
                        questions: res.message
                    });
                }
            })
        },10)
    }
    render() {
        return(
            <div className="zixun clear">
                <div className="wenti">
                    <h2 className="often"></h2>
                    <div className="row">
                    {
                        this.state.questions.map((v,i)=>{
                            return (
                                <div className="col-md-4" style={{padding: '10px'}} key={i}>
                                    <div className="ques">
                                        <h3>{v.title}</h3>
                                        <span></span>
                                        <p dangerouslySetInnerHTML={{ __html: v.content }}></p>
                                        <div className="angle">
                                            <span>{i+1}</span>
                                        </div>
                                    </div>
                                </div>
                            )
                        })
                    }
                    </div>
                </div>
            </div>
        )
    }
}


