import React,{ Component } from 'react';
import './index.css';
import Header from './head/head';
import Footer from './foot/footer';
import Fixnav from './foot/fixnav';
import Fixphone from './foot/fixphone';
import Motai from './head/motai';
import Logreg from './head/logreg';

export default class Base extends Component {
    render() {
        return(
            <div id="base">
                <Header />
                <Footer />
                <Fixnav />
                <Fixphone />
                <Motai />
                <Logreg />
            </div>
        )
    }
}


