import React,{ Component } from 'react';
import $ from 'jquery';
import './fixphone.css';

export default class Fixphone extends Component {
    componentDidMount() {
        run_phone();
        function run_phone() {
            $('.fa-phone-square').removeClass('animated rubberBand');
            setTimeout(function () {
                $('.fa-phone-square').addClass('animated rubberBand')
            }, 1000);
            setTimeout(run_phone, 2000);
        };
    }
    render(){
        return(
            <div className="fix-phone">
                <a href="tel:18052310959">
                    <div className="fa fa-phone-square animated rubberBand" aria-hidden="true">
                        <i className="tel">18052310959</i>
                    </div>
                </a>
            </div>
        )
    }
}


