import React,{ Component } from 'react';
import $ from 'jquery';
import './main_pls.css';

export default class Mainpls extends Component {
    constructor(){
        super();
        this.state = {
            comments: [],
            pls: [
                {
                    avatar:'user1.jpg',
                    name:'-',
                    pl:'',
                    img2:'pl_img1.jpg', 
                    img3:'pl_img2.jpg', 
                    img4:'pl_img5.jpg'
                },
                {
                    avatar:'user2.jpg',
                    name:'-',
                    pl:'',
                    img2:'pl_img3.jpg', 
                    img3:'pl_img4.jpg'
                },
                {
                    avatar:'user3.jpg',
                    name:'-',
                    pl:'',
                    img2:'pl_img6.jpg',
                    img3:'pl_img9.jpg',
                    img4:'pl_img10.jpg'
                },
                {
                    avatar:'user4.jpg',
                    name:'-',
                    pl:'',
                    img2:'pl_img7.jpg',
                    img3:'pl_img8.jpg'
                },
                {
                    avatar:'user5.jpg',
                    name:'-',
                    pl:'',
                    img2:'pl_img11.jpg'
                },
                {
                    avatar:'user.jpg',
                    name:'-',
                    pl:'',
                    img2:'pl_img12.jpg',
                    img3:'pl_img13.jpg',
                },
                {
                    avatar:'user.jpg',
                    name:'-',
                    pl:'',
                    img2:'pl_img14.jpg',
                    img3:'pl_img15.jpg'
                }
            ]
        }
    }
    componentDidMount() {
        const _this = this;
        const hostname = window.location.hostname;
        // change
        $('.pl-list').on('click', '.sm_img', function () {
            $(this).parent().parent().children('.pl_lg_img').children().attr({
                src: $(this).attr('src')
            });
        });
        $('.pl-list').on('click', '.lg_img', function () {
            $(this).attr({
                src: ''
            })
        })
        $.ajax({
            url: 'http://' + hostname + ':3002/comment',
            type: 'get',
            dataType: 'json',
            success: function (res){
                _this.setState({
                    comments: res.message
                })
            }
        })
    }
    render() {
        return(
            <div>
                <h3 style={{marginTop: '80px',textAlign: 'center',fontSize: '34px'}} className="pl"></h3>
                <ul className="pl-list">
                    {
                        this.state.comments.map(function(v,i) {
                            return(
                                <li key={i}>
                                    <img src={v.avatar} alt="" />
                                    <span>{v.name}</span>
                                    <br />
                                    <br />
                                    <b></b><i></i>
                                    <div className="pl_content" dangerouslySetInnerHTML={{ __html: v.content }} ></div>
                                    <div className="pl_img">
                                        <div className="pl_lg_img">
                                            <img src="" className="lg_img" alt="" />
                                        </div>
                                        <hr style={{margin: '0',borderColor: 'transparent'}} />
                                        <div className="pl_imgs">
                                            {v.pl_img1?<img src={v.pl_img1} alt="img2" className="sm_img" />:''}
                                            {v.pl_img2?<img src={v.pl_img2} alt="img3" className="sm_img" />:''}
                                            {v.pl_img3?<img src={v.pl_img3} alt="img4" className="sm_img" />:''}
                                        </div>
                                    </div>
                                </li>
                            )
                        })
                    }
                </ul>
            </div>
        )
    }
}


