import React,{ Component } from 'react';
import $ from 'jquery';
import './main_wenti.css';
import Mainbanner from './main_banner';
import Mainpromise from './main_promise';
import Mainzixun from './main_zixun';

export default class Mainwenti extends Component{
    constructor(){
        super()
        this.state = {
            categorys: [],
            article: []
        }
    }
    componentWillMount(){
        const _this = this;
        const hostname = window.location.hostname;
        c_ajax?c_ajax.abort():'';
        var c_ajax = $.ajax({
            url: 'http://' + hostname + ':3002/category',
            type: 'get',
            dataType: 'json',
            success: function(res) {
                _this.setState({
                    categorys: res.message
                });
            }
        });
        a_ajax?a_ajax.abort():'';
        var a_ajax = $.ajax({
            url: 'http://' + hostname + ':3002/article',
            type: 'get',
            dataType: 'json',
            success: function(res) {
                _this.setState({
                    article: res.message
                });
            }
        });
    }
    componentDidMount(){
        const hostname = window.location.hostname;
        var _this = this;
        setTimeout(()=>{
            $('.itemList li').eq(0).addClass('active');
            $('.itemList li').on('click', function(){
                $('.itemList li').removeClass('active');
                $(this).addClass('active');
                const which = $(this).attr('name');
                _this.ajax ? _this.ajax : '';
                _this.ajax = $.ajax({
                    url: 'http://' + hostname + ':3002/article',
                    type: 'get',
                    dataType: 'json',
                    data: {
                        which: which
                    },
                    success: function(res){
                        _this.setState({
                            article: res.message
                        });
                    }
                });
            })
        },10)
    }
    render(){
        return (
            <div>
                <Mainbanner />
                <div className="list">
                    <div className="prog">
                        <h2 style={{fontSize: '24px'}}>
                            <i> :</i>
                            <ul className="itemList">
                                {
                                    this.state.categorys.map(v=>{
                                        return (
                                            <li key={v._id} name={v.category_name}>{v.category_name}</li>
                                        )
                                    })
                                }
                            </ul>
                        </h2>
                        <div className="gong questions showme">
                            {
                                this.state.article.map((v,i)=>{
                                    return (
                                        <div key={i}>
                                            <h4 style={{padding: '30px 0 10px 0'}}>{v.title}</h4>
                                            <div dangerouslySetInnerHTML={{ __html : v.content}} style={{padding: '30px 60px', lineHeight: '30px', backgroundColor: '#e5e5e5', borderRadius: '4px'}}></div>
                                        </div>
                                    )
                                })
                            }
                        </div>
                        <Mainpromise />
                    </div>
                </div>
                <Mainzixun />
            </div>
        )
    }
}


