import React,{ Component } from 'react';
import './main_banner.css';
import Swiper from '_dirname/../../public/js/swiper.min';
import ccc from "../images/ccc.jpg";
import aaab from "../images/aaab.jpg";
import bbb from "../images/bbb.jpg";

export default class Mainbanner extends Component {
    componentDidMount=()=>{
        let swiper1 = new Swiper('.swiper-container1', {
			loop: true,
			autoplay: true,
			delay: 2000,
			effect: 'fade'
		});
    }
    render(){
        return(
            <div className="banner">
                <div className="banner-wrapper">
                    <div className="swiper-container1">
                        <div className="swiper-wrapper">
                            <div className="swiper-slide">
                                <img src={bbb} alt="bbb" />
                                <div className="b-title b-title1">
                                    <h2>
                                        <span style={{color: '#0b4396'}}></span>
                                    </h2>
                                    <p>   !</p>
                                </div>
                            </div>
                            <div className="swiper-slide">
                                <img src={ccc} alt="ccc" />
                                <div className="b-title b-title2">
                                    <h2></h2>
                                    <p></p>
                                </div>
                            </div>
                            <div className="swiper-slide">
                                <img src={aaab} alt="" />
                                <div className="b-title">
                                    <h2></h2>
                                    <p>++</p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}


