import React,{ Component } from 'react';
import './footer.css';
import ly from '../images/liyang.jpg';

export default class Footer extends Component {
    render(){
        return(
            <div className="footer">
                <div className="foot-wrapper clear">
                    <img src={ly} alt="liyang" className="left" />
                    <div className="left">
                        <p> 18052310959()</p>
                        <p>32-7A503</p>
                        <p>Copyright   All Rights Reserved.  ICP18012412</p>
                    </div>
                </div>
            </div>
        )
    }
}


