import React,{ Component } from 'react';
import $ from 'jquery';
import './hyzx-category.css';
import hot from '../images/hot.png';

export default class Hyzxcategory extends Component {
    constructor(props){
        super(props);
        this.state = {
            item: {
                hot: this.props.item.hot,
                category_name: this.props.item.category_name,
                article_id: this.props.item.article_id
            },
            articles: [],
            article: {},
            prevarticle: {},
            nextarticle: {},
            iniNum: 0,
            eachNum: 8,
            allNum: 0
        }
    }
    componentWillReceiveProps(nextProps){
        const _this = this;
        setTimeout(()=>{
            var iniNum = _this.state.iniNum;
            var eachNum = _this.state.eachNum;
            const hostname = window.location.hostname;
            var which = '';
            const hot = nextProps.item.hot;
            const category_name = nextProps.item.category_name;
            const article_id = nextProps.item.article_id;
            if(hot) {
                which = 'WHERE hot = 1';
                _this.hot_ajax?_this.hot_ajax.abort():'';
                _this.hot_ajax = $.ajax({
                    url: 'http://' + hostname + ':3002/hot_articles',
                    type: 'get',
                    data: {
                        iniNum: 0,
                        eachNum: eachNum
                    },
                    dataType: 'json',
                    success: function(results){
                        _this.setState({
                            item: nextProps.item,
                            articles: results.message,
                            allNum: results.allNum,
                            iniNum: 0
                        });
                    }
                });
            }else if(category_name) {
                which = 'WHERE category_name = "' + category_name + '"';
                _this.category_ajax?_this.category_ajax.abort():'';
                _this.category_ajax = $.ajax({
                    url: 'http://' + hostname + ':3002/zx_articles',
                    type: 'get',
                    data: {
                        which: which,
                        iniNum: 0,
                        eachNum: eachNum
                    },
                    dataType: 'json',
                    success: function(results) {
                        _this.setState({
                            item: nextProps.item,
                            articles: results.message,
                            allNum: results.allNum,
                            iniNum: 0
                        })
                    }
                });
            }else if( article_id) {
                which = article_id;
                _this.article_ajax?_this.article_ajax.abort():'';
                _this.article_ajax = $.ajax({
                    url: 'http://' + hostname + ':3002/zx_article',
                    type: 'get',
                    data: {
                        which: which
                    },
                    dataType: 'json',
                    success: function (results) {
                        var articles = results.message;
                        if(articles.length == 1) {
                            if(articles[0]._id == which){
                                _this.setState({
                                    item: {
                                        hot: 0,
                                        category_name: '',
                                        article_id: which
                                    },
                                    article: articles[0],
                                    prevarticle: {},
                                    nextarticle: {}
                                })
                            }
                        }else if(articles.length == 2) {
                            if(articles[0]._id == which){
                                _this.setState({
                                    item: nextProps.item,
                                    article: articles[0],
                                    prevarticle: {},
                                    nextarticle: articles[1]
                                })
                            }else if(articles[1]._id == which){
                                _this.setState({
                                    item: nextProps.item,
                                    article: articles[1],
                                    prevarticle: articles[0],
                                    nextarticle: {}
                                })
                            }
                        }else {
                            _this.setState({
                                item: nextProps.item,
                                article: articles[1],
                                prevarticle: articles[0],
                                nextarticle: articles[2]
                            })
                        }
                    }
                });
            }
        },30);
    }
    componentWillMount(){
      
    }
    componentWillUpdate(){
        const _this = this;
        const hostname = window.location.hostname;
        setTimeout(()=>{
            $('.zx-article-item,.find_which_article').on('click',function(){
                var article_id = Number($(this).attr('_id'));
                var which = article_id;
                _this.articl_ajax?_this.articl_ajax.abort():'';
                _this.articl_ajax = $.ajax({
                    url: 'http://' + hostname + ':3002/zx_article',
                    type: 'get',
                    data: {
                        which: which
                    },
                    dataType: 'json',
                    success: function(results) {
                        var articles = results.message;
                        if(articles.length == 1) {
                            if(articles[0]._id == which){
                                _this.setState({
                                    item: {
                                        hot: 0,
                                        category_name: '',
                                        article_id: which
                                    },
                                    article: articles[0],
                                    prevarticle: {},
                                    nextarticle: {}
                                })
                            }
                        }else if(articles.length == 2) {
                            if(articles[0]._id == which){
                                _this.setState({
                                    item: {
                                        hot: 0,
                                        category_name: '',
                                        article_id: which
                                    },
                                    article: articles[0],
                                    prevarticle: {},
                                    nextarticle: articles[1]
                                })
                            }else if(articles[1]._id == which){
                                _this.setState({
                                    item: {
                                        hot: 0,
                                        category_name: '',
                                        article_id: which
                                    },
                                    article: articles[1],
                                    prevarticle: articles[0],
                                    nextarticle: {}
                                })
                            }
                        }else {
                            _this.setState({
                                item: {
                                    hot: 0,
                                    category_name: '',
                                    article_id: which
                                },
                                article: articles[1],
                                prevarticle: articles[0],
                                nextarticle: articles[2]
                            })
                        }
                    }
                });
            });
        },10)
        $('.prev_page').on('click', function () {
            var iniNum = _this.state.iniNum;
            var eachNum = _this.state.eachNum;
            var which = '';
            const hot = _this.state.item.hot;
            const category_name = _this.state.item.category_name;
            const article_id = _this.state.item.article_id;
            if(iniNum <= 0) {
                return
            }
            iniNum -= eachNum;
            if(hot) {
                which = 'WHERE hot = 1';
                _this.hot_ajax?_this.hot_ajax.abort():'';
                _this.hot_ajax = $.ajax({
                    url: 'http://' + hostname + ':3002/hot_articles',
                    type: 'get',
                    data: {
                        iniNum: iniNum,
                        eachNum: eachNum
                    },
                    dataType: 'json',
                    success: function(results){
                        _this.setState({
                            articles: results.message,
                            allNum: results.allNum,
                            iniNum: iniNum
                        });
                    }
                });
            }else if(category_name) {
                which = 'WHERE category_name = "' + category_name + '"';
                _this.category_ajax?_this.category_ajax.abort():'';
                _this.category_ajax = $.ajax({
                    url: 'http://' + hostname + ':3002/zx_articles',
                    type: 'get',
                    data: {
                        which: which,
                        iniNum: iniNum,
                        eachNum: eachNum
                    },
                    dataType: 'json',
                    success: function(results) {
                        _this.setState({
                            articles: results.message,
                            allNum: results.allNum,
                            iniNum: iniNum
                        })
                    }
                });
            }
        });
        $('.next_page').on('click', function () {
            var iniNum = _this.state.iniNum;
            var eachNum = _this.state.eachNum;
            var allNum = _this.state.allNum;
            var which = '';
            const hot = _this.state.item.hot;
            const category_name = _this.state.item.category_name;
            const article_id = _this.state.item.article_id;
            if((iniNum + eachNum) >= allNum) {
                return
            }
            iniNum += eachNum;
            if(hot) {
                which = 'WHERE hot = 1';
                _this.hot_ajax?_this.hot_ajax.abort():'';
                _this.hot_ajax = $.ajax({
                    url: 'http://' + hostname + ':3002/hot_articles',
                    type: 'get',
                    data: {
                        iniNum: iniNum,
                        eachNum: eachNum
                    },
                    dataType: 'json',
                    success: function(results){
                        _this.setState({
                            articles: results.message,
                            allNum: results.allNum,
                            iniNum: iniNum
                        });
                    }
                });
            }else if(category_name) {
                which = 'WHERE category_name = "' + category_name + '"';
                _this.category_ajax?_this.category_ajax.abort():'';
                _this.category_ajax = $.ajax({
                    url: 'http://' + hostname + ':3002/zx_articles',
                    type: 'get',
                    data: {
                        which: which,
                        iniNum: iniNum,
                        eachNum: eachNum
                    },
                    dataType: 'json',
                    success: function(results) {
                        _this.setState({
                            articles: results.message,
                            allNum: results.allNum,
                            iniNum: iniNum
                        })
                    }
                });
            }
        });
    }
    componentDidMount(){

    }
    componentWillUnmount(){

    }
    render(){
        let hideme = {
            display: 'none'
        }
        let showme = {
            display: 'block'
        }
        return (
            <div className="hyzx-detail left">
                <div className="hyzx-article-detail" style={ !this.state.item.article_id ? hideme : showme }>
                    <h2>{this.state.article.title?this.state.article.title:''}</h2>
                    <div className="time-author clear">
                        <span className="fa fa-clock-o left">{this.state.article.c_time?this.state.article.c_time:''}</span>
                        <span className="fa fa-eye right">{this.state.article.read_num?this.state.article.read_num:''}</span>
                        <span className="right">&nbsp;&nbsp;&nbsp;</span>
                    </div>
                    <div className="hyzx-article_id" dangerouslySetInnerHTML={{ __html: (this.state.article.content?this.state.article.content:'') }}></div>
                    <div className="prev-next clear">
                        {this.state.prevarticle._id?<span className="find_which_article fa fa-angle-double-up left" _id={this.state.prevarticle._id}>  : <i>{this.state.prevarticle.title}</i></span>:''}
                        {this.state.nextarticle._id?<span className="find_which_article fa fa-angle-double-down right" _id={this.state.nextarticle._id}>  : <i>{this.state.nextarticle.title}</i></span>:''}
                    </div>
                </div>


                <div className="hyzx-category-wrapper" style={ !this.state.item.article_id ? showme : hideme }>
                    <ul className="zx-article-lists">
                        {
                            this.state.articles.map(v=>{
                                return (
                                    <li className="zx-article-item clear" key={v._id} _id={v._id}>
                                        <img src={ v.bg_img } className="left" alt=""/>
                                        <div className="left">
                                            <h4 className="clear"><span className="left">{v.title}</span><img src={ v.hot?hot:{} } className="left" alt=""/></h4>
                                            <p className="zx-article-item-content" dangerouslySetInnerHTML={{ __html: v.content }}></p>
                                            <div className="clear">
                                                <span className="fa fa-clock-o left"> {v.c_time}</span>
                                                <span className="fa fa-eye left"> {v.read_num}</span>
                                            </div>
                                        </div>
                                    </li>
                                )
                            })
                        }
                    </ul>
                    <ul className="pager">
                        <li className="prev_page"><a></a></li>&nbsp;&nbsp;&nbsp;&nbsp;
                        <li className="next_page"><a></a></li>
                    </ul>
                </div>
            </div>
        )
    }
}


